@import '../../mixins.scss';

.list {
  background: #ffffff;
  border: 1px solid rgba(51, 74, 86, 0.08);
  box-shadow: 2px 2px 16px rgba(51, 74, 86, 0.05);
  border-radius: 2.4rem;
  margin-bottom: 0.8rem;
  padding: 1.6rem;

  &.active {
    .list__header-down {
      transform: rotate(-180deg);
    }

    .list__content {
      display: flex;
    }
  }

  &__item {}

  &__header {
    display: flex;
    width: 100%;
    justify-content: space-between;
    align-items: center;

    &-down {
      margin-left: 0.8rem;
      cursor: pointer;
      transition: $t transform;
    }

    &-sys {
      color: $primary;
      display: flex;
      align-items: center;
      justify-content: center;
    }

    &-name {
      display: flex;
      gap: 0 0.4rem;
      @include tg-12-medium;
      margin-bottom: 0.4rem;
    }

    &-cost {
      display: flex;
      gap: 0 0.4rem;
      @include tg-12-medium;

      &>div {
        color: rgba($primary, 0.6);
      }
    }
  }

  &__content {
    // display: flex;
    display: none;
    border-top: 1px solid rgba(#334a56, 0.2);
    width: 100%;
    flex-direction: column;
    margin-top: 0.8rem;
    padding-top: 0.8rem;

    &-row {
      display: flex;
      align-items: center;
      justify-content: space-between;

      &:not(:last-child) {
        margin-bottom: 0.8rem;
      }

      &>span {
        @include tg-12-medium;
        color: rgba($primary, 0.6);
      }

      &>a {
        @include tg-12-semibold;
        text-decoration: underline;
      }
    }

    &-time {
      color: #334a56;
      @include tg-12-medium;
    }

    &-quantity {
      color: #334a56;
      @include tg-12-semibold;
    }
  }
}

.table {
  text-align: left;

  &__head {
    position: sticky;
    top: 0;
    background-color: $white;
    backdrop-filter: blur(12px);
    @include tg-14-medium;
    z-index: 10;
  }

  &__row {
    display: flex;
    align-items: center;
    @include tg-16-medium;

    &-header {
      border-bottom: 1px solid rgba($primary, 0.2);
      opacity: 0.6;
    }
  }

  &__content {
    overflow: hidden;
    text-overflow: ellipsis;
  }

  &__col {
    color: $primary;
    padding: 0.8rem 0;
    flex-basis: 0;
    flex-grow: 1;
    color: inherit;
  }

  &__component {
    width: 100%;
  }
}