@import '../mixins.scss';

.section__front {
  @include lg {
    padding: 0;
  }

  .title__sub {
    margin-top: 14.4rem;

    @include lg {
      margin-top: 3.2rem;
      margin-bottom: 0.4rem;
      @include tg-12-medium;
    }
  }
}

.desc {
  margin-bottom: 5.2rem;

  @include lg {
    margin-bottom: 2.4rem;
  }
}

.btns {
  &__connect {
    margin-right: 4rem;
  }
}

.stat {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  gap: 0 4.4rem;
  margin-top: 6rem;

  @include lg {
    margin-top: 2.4rem;
    margin-bottom: 4rem;
    // gap: 0 4rem;
    gap: unset;
    width: 100%;
    justify-content: space-between;
  }

  &__title {
    @include tg-32-medium;

    @include lg {
      @include tg-20-medium;
    }
  }

  &__desc {
    @include tg-14-normal;
    color: rgba($primary, 0.6);
  }

  &__delimetr {
    border-left: 1px solid rgba($primary, 0.2);
    height: 4.4rem;
  }
}

.front__swiper {
  position: relative;
  z-index: 10;
  margin-left: 6rem;
  width: 56.8rem;

  @include lg {
    width: 100%;
    margin: 0;
  }

  &:deep(.swiper-slide) {
    width: 56.8rem;
    padding: 0 2.4rem 3.2rem;
    box-sizing: border-box;

    @include lg {
      width: 100%;
      padding-bottom: 5rem;
    }
  }

  &-slide {
    @include lg {}

    img {
      @include lg {
        width: 28rem;
        height: 28rem;
        display: block;
        margin: 0 auto;
      }
    }
  }

  &-img {
    border-radius: 50%;
    box-shadow: 0px 12px 24px rgba(51, 74, 86, 0.36);
  }

  &-elements {
    display: flex;
    margin-top: 5.2rem;
    position: relative;

    @include lg {
      margin-top: 2rem;
      height: 3.2rem;
    }
  }

  &-navs {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 0 1.2rem;
    position: relative;
    z-index: 5;

    @include lg {
      z-index: unset;
      position: absolute;
      width: 100%;
      display: block;
    }
  }

  &-prev {
    @include lg {
      left: 0;
      position: absolute;
    }
  }

  &-next {
    @include lg {
      right: 0;
      position: absolute;
    }
  }

  &-pag {
    flex-grow: 1;

    @include lg {
      max-width: calc(100% - 6.6rem);
      margin: 0 auto;
      position: relative;
      z-index: 10;
    }
  }

  &-bet {
    position: absolute;
    bottom: 1.2rem;
    left: calc(50% - 14rem);

    @include lg {
      bottom: 0rem;
    }
  }
}

.front__slider {
  position: relative;

  &-el1 {
    position: absolute;
    width: 44rem;
    height: 52rem;
    background: linear-gradient(143.82deg, #e3ecf0 9.24%, #cfdce1 100.16%);
    top: 4.8rem;
    left: 2.8rem;
    border-radius: 22rem 22rem 0 0;
    z-index: 3;

    @include lg {
      display: none;
    }
  }

  &-el2 {
    position: absolute;
    width: 44rem;
    height: 52rem;
    background: linear-gradient(143.82deg, #e3ecf0 9.24%, #cfdce1 100.16%);
    opacity: 0.32;
    top: 2rem;
    left: 5.6rem;
    border-radius: 22rem 22rem 0 0;
    z-index: 2;

    @include lg {
      display: none;
    }
  }

  &-el3 {
    position: absolute;
    width: 44rem;
    height: 52rem;
    border: 1px solid rgba(51, 74, 86, 0.2);
    top: 7.6rem;
    left: 0rem;
    border-radius: 22rem 22rem 0 0;
    z-index: 1;

    @include lg {
      display: none;
    }
  }
}