@import '../../mixins.scss';

.table {
  text-align: left;

  &__head {
    position: sticky;
    top: 0;
    background-color: $white;
    backdrop-filter: blur(12px);
    @include tg-14-medium;
    z-index: 10;
  }

  &__row {
    display: flex;
    align-items: center;
    @include tg-16-medium;

    &-header {
      border-bottom: 1px solid rgba($primary, 0.2);
      opacity: 0.6;
    }
  }

  &__content {
    overflow: hidden;
    text-overflow: ellipsis;
  }

  &__col {
    color: $primary;
    padding: 0.8rem 0;
    flex-basis: 0;
    flex-grow: 1;
    color: inherit;
  }

  &__component {
    width: 100%;
  }
}