@import '../../mixins.scss';

.mobilemenu {
  position: absolute;
  left: 0;
  top: 6.4rem;
  width: 100%;
  max-height: calc(100% - 6.4rem);
  min-height: calc(100% - 6.4rem);
  overflow-y: auto;
  background-color: rgba($white, 0.96);
  transition: $t transform;
  display: flex;
  flex-direction: column;
  z-index: 300;

  //   filter: blur(24px);
  &__item {
    text-transform: uppercase;
  }

  &__nav {
    padding: 1.6rem;
  }

  &__item {
    text-align: center;
    padding: 0 0 1.6rem;
  }
}