@import '../../mixins.scss';

.isliked {
  width: 2.4rem;
  height: 2.4rem;
  background: $white;
  border-radius: 50%;
  color: $primary;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;

  @include lg {
    width: 2rem;
    height: 2rem;
  }

  &__icon {
    @include lg {
      width: 1.6rem;
      height: 1.6rem;
      min-width: 1.6rem !important;
      min-height: 1.6rem !important;
    }
  }
}