@import '../../../mixins.scss';

.cardr {
  display: flex;
  height: 100%;
  padding-bottom: 1.6rem;

  @include lg {
    padding-bottom: 1.2rem;
  }

  &__inner {
    width: 100%;
    display: flex;
    background: #ffffff;
    box-shadow: 2px 2px 16px rgba(51, 74, 86, 0.05);
    border-radius: 2.2rem;
    padding: 2.4rem;
    border: 1px solid transparent;
    transition: $t all;
    position: relative;

    @include lg {
      padding: 1.2rem 1.2rem 4.4rem;
    }

    &:hover {
      box-shadow: 2px 2px 32px rgba(51, 74, 86, 0.17);
      border-color: rgba(51, 74, 86, 0.32);
    }
  }

  &__badges {
    margin-top: auto;
    padding-bottom: 1.6rem;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
  }

  &__like {
    position: absolute;
    top: 1.2rem;
    right: 1.2rem;

    @include lg {
      top: 0.8rem;
      right: 0.8rem;
    }
  }

  &__type {
    position: absolute;
    display: inline-block;
    @include tg-14-semibold;
    padding: 0.4rem 0.8rem;
    background: rgba(51, 74, 86, 0.92);
    border-radius: 0.8rem;
    text-transform: uppercase;
    color: $white;
    top: 1.2rem;
    left: 1.2rem;

    @include lg {
      top: unset;
      left: unset;
      position: relative;
      @include create-font(1rem, 600, 1.6rem);
    }
  }

  &__img {
    position: relative;
    display: flex;
    margin-right: 2.4rem;
    width: 18.4rem;
    min-width: 18.4rem;
    height: 12rem;
    object-fit: cover;
    overflow: hidden;
    border-radius: 1.2rem;

    @include lg {
      width: 10rem;
      min-width: 10rem;
      height: 10rem;
      margin-right: 1.2rem;
    }

    img {
      width: 100%;
      height: auto;
      width: 25rem;
      height: 20rem;
      object-fit: cover;
    }
  }

  &__title {
    @include tg-16-medium;
    margin-bottom: 0.4rem;
    overflow: hidden;

    span {
      @include lg {
        display: inline-block;
        text-overflow: ellipsis;
        white-space: nowrap;
        max-width: 15rem;
        overflow: hidden;
      }
    }

    @include lg {
      display: flex;
      align-items: center;
      justify-content: space-between;
    }
  }

  &__items {
    @include tg-12-unormal;
    color: rgba($primary, 0.6);
    margin-bottom: 1.6rem;
  }

  &__box {
    flex-grow: 1;
    display: flex;

    @include lg {
      flex-direction: column;
    }
  }

  &__content {
    flex-grow: 1;
    display: flex;
    flex-direction: column;
    padding-right: 2.4rem;

    @include lg {
      display: block;
      max-width: 100%;
      padding: 0;
    }
  }

  &__actions {
    min-width: 25rem;
    display: flex;
    flex-direction: column;

    @include lg {
      min-width: unset;
    }
  }

  &__badges {
    flex-grow: 1;
  }

  &__btn {
    @include lg {
      position: absolute;
      bottom: 1.2rem;
      width: calc(100% - 2.4rem);
    }

    .button {
      width: 100%;
    }
  }
}