@import '../../mixins.scss';

.dropdown {
  position: relative;
  min-width: 26rem;
  @include tg-16-medium;

  &__style {
    position: absolute;
    background: #ffffff;
    border: 1px solid rgba(51, 74, 86, 0.32);
    box-shadow: 2px 2px 24px rgba(51, 74, 86, 0.08);
    border-radius: 24px;
    min-height: 4.8rem;
    top: 0;
    left: 0;
    right: 0;
    z-index: 2;
  }

  &__header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    z-index: 5;
    padding: 1rem 1.6rem;
    // position: absolute;
    cursor: pointer;
    height: 4.8rem;
    position: relative;
  }

  &__body {
    position: relative;
    border-top: 1px solid rgba($primary, 0.2);
    padding-top: 1.2rem;
    margin: 4.4rem 1.6rem 1.6rem;
  }

  &__icon {
    transition: transform $t;

    &-active {
      transform: rotate(-180deg);
    }
  }

  &__line {
    color: rgba($primary, 0.6);
    transition: $t color;
    cursor: pointer;

    &.selected {
      color: $primary;
    }
  }
}

.v-enter-active,
.v-leave-active {
  transition: opacity 0.5s ease;
}

.v-enter-from,
.v-leave-to {
  opacity: 0;
}