@import '../../../mixins.scss';

.filter {
  width: 100%;

  &__inner {
    background-color: $white;
    box-shadow: 2px 2px 16px rgba(51, 74, 86, 0.05);
    padding: 2.4rem;
    border-radius: 2.4rem;
  }
}

.field {
  display: flex;
  gap: 0 1.6rem;
  padding: 0.8rem 0;
  width: 100%;
  align-items: center;
  justify-content: space-between;

  &__name {
    @include tg-20-semi;
    font-weight: 600;
    order: 1;
  }

  &__check {
    order: 2;
  }

  &__texts {
    margin-top: -0.4rem;
  }
}