@import '../../mixins.scss';

.category {
  &__header {
    display: flex;
    align-items: center;
    justify-content: space-between;
    user-select: none;
    cursor: pointer;
  }

  &__title {
    @include tg-20-semi;
    color: $primary;
  }

  &__arrow {
    transition: $t transform;
  }

  &__content {
    overflow: hidden;
    transition: $t max-height;
    max-height: 0px;
  }

  &-active {
    .category__content {
      overflow-y: auto;
    }

    .category__arrow {
      transform: rotate(180deg);
    }
  }
}