@import './mixins.scss';

@mixin tg-40-medium {
  @include create-font(4rem, 500, 4.8rem);
}
@mixin tg-32-medium {
  @include create-font(3.2rem, 500, 4rem);
}
@mixin tg-24-medium {
  @include create-font(2.4rem, 500, 4rem);
}
@mixin tg-20-semi {
  @include create-font(2rem, 600, 2.4rem);
}
@mixin tg-20-bold {
  @include create-font(2rem, 700, 2.4rem);
}
@mixin tg-20-normal {
  @include create-font(2rem, 600, 2.8rem);
}
@mixin tg-20-medium {
  @include create-font(2rem, 500, 2.8rem);
}
@mixin tg-20-bold {
  @include create-font(2rem, bold, 2.8rem);
}
@mixin tg-16-semibold {
  @include create-font(1.6rem, 600, 2.4rem);
}
@mixin tg-16-medium {
  @include create-font(1.6rem, 500, 2.8rem);
}
@mixin tg-14-semibold {
  @include create-font(1.4rem, 600, 2rem);
}
@mixin tg-14-normal {
  @include create-font(1.4rem, 400, 2rem);
}
@mixin tg-14-medium {
  @include create-font(1.4rem, 500, 2rem);
}
@mixin tg-12-normal {
  @include create-font(1.2rem, 400, 2.4rem);
}
@mixin tg-12-medium {
  @include create-font(1.2rem, 500, 2rem);
}
@mixin tg-12-semibold {
  @include create-font(1.2rem, 600, 2rem);
}
@mixin tg-12-unormal {
  @include create-font(1.2rem, 400, 2rem);
}
@mixin tg-12-bold {
  @include create-font(1.2rem, 700, 2.4rem);
}
