:root {
  --safe-area-top: env(safe-area-inset-top, 44px);
  --safe-area-bottom: env(safe-area-inset-bottom, 34px);
}
html {
  font-size: 62.5%;
}

body {
  overflow-x: hidden;
  font-variant: normal;
  font-feature-settings: normal;
  background: $light;
  @include create-font(1.4rem, 400, 2.8rem);
  color: $primary;
}

#app {
  position: relative;
  min-height: 100vh;
  display: flex;
  width: 100vw;
}

.app-modal-container {
  overflow: hidden;
}

[data-simplebar]:not(.simplebar-dragging) .simplebar-content-wrapper {
  scroll-behavior: smooth;
}

.simplebar-track.simplebar-vertical .simplebar-scrollbar::before {
  background-color: rgba($primary, 0.6);
  width: 2px;
}

.simplebar-scrollbar.simplebar-visible::before {
  opacity: 0.6 !important;
}

.simplebar-track.simplebar-vertical .simplebar-scrollbar::before {
  left: 9px;
}
