@import '../../mixins.scss';

.badges {
  display: flex;
  width: 100%;
  align-items: center;
  margin-top: 0.4rem;
  @include tg-14-normal;
  justify-content: space-between;

  @include lg {
    @include tg-12-unormal;
  }

  &__title {
    display: flex;
    align-items: center;
    justify-content: center;
  }

  &__desc {
    display: flex;
    display: flex;
    align-items: center;
    justify-content: center;
  }

  &__img {
    margin-right: 0.4rem;
  }

  // variants of badges
  &-opacity {
    opacity: 0.4;
  }

  &-variant1 {
    .badges__title {
      @include tg-20-normal;

      @include lg {
        @include tg-14-normal;
      }
    }

    @include lg {
      flex-direction: column;
      align-items: flex-start;
    }

    .badges__img {
      @include lg {
        width: 2rem;
        height: 2rem;
      }
    }
  }
}