@import '../../mixins.scss';

.header {
  position: relative;
  top: 0;
  z-index: 100;
  background: rgba($white, 0.96);
  box-shadow: 0px 4px 14px rgba(51, 74, 86, 0.03);
  padding: 2.2rem 0;

  &__connect {
    margin-left: 2rem;

    @include xl {
      margin-left: 0rem;
    }
  }

  @include lg {
    padding: 0;
    height: 6.4rem;
    display: flex;
    width: 100%;
    align-items: center;
  }

  &__svg {
    color: $primary;
  }
}

.menu {
  display: flex;
  width: 100%;
  gap: 0 4rem;
  justify-content: center;

  @include xl {
    gap: 0 2.4rem;
  }

  &__item {
    color: rgba($primary, 0.6);
    transition: $t;
  }
}

.mobile-menu {

  &-enter-from,
  &-leave-to {
    transform: translateY(100%);
  }
}

.change {

  &-enter-active,
  &-leave-active {
    transition: 0.1s ease transform;
  }

  &-enter-from,
  &-leave-to {
    transform: scale(0);
  }
}