@import '../../mixins.scss';

.checkbox {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 18px;
  height: 18px;
  transition: $t background-color;
  background-color: transparent;
  border: 1px solid $primary;
  border-radius: 4px;
  cursor: pointer;

  &__icon {
    transition: $t opacity;
    opacity: 0;
    color: $white;
  }

  &__self {
    display: none;
  }

  &-active {
    background-color: $primary;

    .checkbox__icon {
      opacity: 1;
    }
  }
}