@import '../../../mixins.scss';

.currency {
  @include tg-16-semibold;
  gap: 0.4rem;
  color: $primary;

  @include lg {
    margin-top: 2rem;
    display: flex;
    flex-direction: column;
  }

  &__total {
    @include lg {
      text-decoration: underline;
      @include tg-16-semibold;
      width: 100%;
      text-align: center;
    }
  }

  &__crypto,
  &__fiat {
    display: flex;
    align-items: center;
  }

  &__fiat {
    margin-top: 0.8rem;

    @include lg {
      margin: 0;
    }
  }
}