@import '../../../mixins.scss';

.card {
  display: flex;
  height: 100%;
  padding-bottom: 1.6rem;

  &__inner {
    width: 100%;
    // height: 100%;
    display: flex;
    flex-direction: column;
    background: #ffffff;
    box-shadow: 2px 2px 16px rgba(51, 74, 86, 0.05);
    border-radius: 2.2rem;
    padding: 2.4rem;
    // height: calc(100% - 1.6rem);
    border: 1px solid transparent;
    transition: $t all;

    &:hover {
      box-shadow: 2px 2px 32px rgba(51, 74, 86, 0.17);
      border-color: rgba(51, 74, 86, 0.32);
    }

    @include lg {
      padding: 0.8rem;
    }
  }

  &__badges {
    margin-top: auto;
    padding-bottom: 1.6rem;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;

    @include lg {
      padding-bottom: 0.8rem;
    }
  }

  &__like {
    position: absolute;
    top: 1.2rem;
    right: 1.2rem;
  }

  &__type {
    position: absolute;
    display: inline-block;
    @include tg-14-semibold;
    padding: 0.4rem 0.8rem;
    background: rgba(51, 74, 86, 0.92);
    border-radius: 0.8rem;
    text-transform: uppercase;
    color: $white;
    top: 1.2rem;
    left: 1.2rem;
  }

  &__img {
    position: relative;
    display: flex;
    width: 100%;
    margin-bottom: 1.6rem;

    img {
      width: 100%;
      height: auto;
    }

    @include lg {
      margin-bottom: 1.2rem;
    }
  }

  &__title {
    @include tg-16-medium;
    margin-bottom: 0.4rem;
    white-space: nowrap;
    text-overflow: ellipsis;
    overflow: hidden;

    @include lg {
      @include tg-12-medium;
      margin-bottom: 1.2rem;
    }
  }

  &__items {
    @include tg-12-unormal;
    color: rgba($primary, 0.6);
    margin-bottom: 1.6rem;

    @include lg {
      position: absolute;
      display: inline-block;
      margin: 0;
      bottom: 1.2rem;
      left: 1.2rem;
      @include create-font(1rem, 500, 1.8rem);
      background: rgba(255, 255, 255, 0.92);
      border-radius: 2.4rem;
      padding: 0 0.4rem;
    }
  }

  &__content {
    flex-grow: 1;
    display: flex;
    flex-direction: column;
  }

  &__badges {
    flex-grow: 1;
  }

  &__btn {
    .button {
      width: 100%;
    }
  }
}