@import '../../mixins.scss';

.textarea {
  background-color: $white;
  border: 0.5px solid rgba(125, 125, 125, 0.32);
  box-shadow: 2px 2px 16px rgba(51, 74, 86, 0.05);
  border-radius: 2.4rem;
  resize: none;
  width: 100%;
  padding: 1.2rem;
  @include tg-14-medium;

  &__label {
    @include tg-12-medium;
    color: rgba($primary, 0.6);
    margin-bottom: 4px;
  }

  &::placeholder {
    width: 100%;
    color: rgba($primary, 0.4);
  }
}