.section {
  display: block;
  position: relative;
  padding: 6rem 0;
  @include lg {
    padding: 3.2rem 0;
  }
  &__front {
    overflow: hidden;
  }
  &__auctions {
    padding: 6rem 0;
    @include lg {
      padding: 3.2rem 0;
    }
  }
  &__lots {
    padding: 6rem 0;
    @include lg {
      padding: 3.2rem 0;
    }
  }
  &__header {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 3.2rem;
    .title {
      margin-bottom: 0;
    }
    @include lg {
      margin-bottom: 1.6rem;
    }
  }
  &__selectors {
  }
  &__settings {
    color: $primary;
    cursor: pointer;
    @include lg {
      margin-left: 1.6rem;
    }
  }
  &__sidebartoggler {
    padding-left: 4rem;
    margin-right: auto;
  }
  &__footer {
    padding-top: 4rem;
    display: flex;
    width: 100%;
    justify-content: center;
    @include lg {
      padding-top: 1.2rem;
    }
  }
}
