@import '../../mixins.scss';

.input {
  display: flex;
  align-items: center;
  border-radius: 6.4rem;
  background-color: $white;
  border: 0.5px solid rgba(125, 125, 125, 0.32);
  box-shadow: 2px 2px 16px rgba(51, 74, 86, 0.05);
  padding: 1.6rem 1.2rem;
  width: 100%;

  &:not([disabled='true']) {
    cursor: text;
  }

  &__label {
    @include tg-12-medium;
    color: rgba($primary, 0.6);
    margin-bottom: 4px;
  }

  &__self {
    @include tg-12-medium;
    width: 100%;

    &::placeholder {
      color: rgba($primary, 0.4);
    }
  }
}