@import '../../mixins.scss';

.field {
  transition: $t opacity;
  display: flex;
  align-items: center;
  opacity: 0.7;

  &__info {
    display: flex;
    align-items: center;
    flex-grow: 1;
    gap: 0 8px;
    justify-content: space-between;
    width: 100%;
  }

  &__name {
    color: $primary;
    @include tg-16-medium;
    margin-right: auto;
  }

  &-selected {
    opacity: 1;
  }
}