@import '../../mixins.scss';

.soc {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 0 1.6rem;

  &__link {
    display: flex;
    justify-content: center;
    align-items: center;
    color: $primary;
    width: 2.8rem;
    height: 2.8rem;
    background: rgba(51, 74, 86, 0.08);
    transition: $t background-color;
    border-radius: 50%;

    &:hover {
      background: rgba(51, 74, 86, 0.16);
    }
  }
}