@import '../../mixins.scss';

.selector {
  display: flex;
  align-items: center;
  gap: 0 1.2rem;

  @include lg {
    gap: 0 0.8rem;
  }

  &__item {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 4.8rem;
    height: 4.8rem;
    border: 1px solid $primary;
    transition: $t background;
    color: $primary;
    cursor: pointer;
    border-radius: 50%;

    @include lg {
      width: 2.4rem;
      height: 2.4rem;
    }

    &.active {
      background: $primary;
      color: $white;
    }
  }

  &__icon {
    @include lg {
      width: 1.6rem !important;
      height: 1.6rem !important;
      min-width: 1.6rem !important;
      min-height: 1.6rem !important;
    }
  }
}