@import '../../mixins.scss';

.button {
  @include tg-14-semibold;
  transition: $t background-color;

  &-base {
    padding: 1.2rem 3.4rem;
    background-color: $primary;
    border-radius: 5rem;
    color: $white;

    &:hover {
      background-color: #24343c;
    }

    &:active {
      background-color: #546771;
    }
  }

  &-border {
    padding: 1.1rem 3.4rem;
    border-radius: 5rem;
    color: $primary;
    border: 1px solid $primary;
    background-color: $white;

    &:hover {
      background-color: $primary;
      color: $white;
    }

    &:active {
      background-color: #546771;
      color: $white;
    }
  }

  &-red {
    background-color: $red;
    padding: 1.2rem 3.4rem;
    @include tg-14-semibold;
    color: $white;
  }

  &-green {
    background-color: $green;
    padding: 1.2rem 3.4rem;
    @include tg-14-semibold;
    color: $white;
  }

  &-white {
    padding: 1.1rem 2rem;
    color: $primary;
    border: 1px solid $primary;
    border-radius: 5rem;
    transition: $t color border;

    &:hover {
      border: 1px solid #24343c;
      color: #24343c;
    }

    &:active {
      color: #546771;
      border: 1px solid #546771;
    }
  }

  &.disabled,
  &[disabled] {
    opacity: 0.6;
    cursor: not-allowed;
  }

  &-size {
    &-large {
      padding: 1.6rem 3.4rem;
    }

    &-medium {
      padding: 1.2rem 3.4rem;

      @include lg {
        padding: 0.8rem 2rem;
      }
    }

    &-small {}
  }
}