@import '../../mixins.scss';

.appfooter {
  padding: 0 0 2.2rem;
  margin-top: auto;

  @include lg {
    padding-bottom: 3.2rem;
  }

  &__logo {
    @include lg {
      text-align: center;
      margin-bottom: 1.2rem;
    }
  }

  &__soc {
    @include lg {
      // justify-content: space-between;
    }
  }

  &-line {
    opacity: 0.1;
    border-top: 1px solid $primary;
    margin: 0 0 2.2rem;

    @include lg {
      padding-bottom: 1.2rem;
    }
  }

  &__copy {
    @include tg-12-semibold;
    margin-top: 3rem;

    @include lg {
      text-align: center;
      margin-top: 2.4rem;
    }
  }

  &__links {
    display: flex;
    gap: 0 2.4rem;
    justify-content: flex-end;
    margin-top: 3rem;

    @include lg {
      justify-content: center;
      margin-top: 2.4rem;
    }

    .common-link {
      @include tg-12-semibold;
    }
  }
}

.author {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  color: $primary;
  @include tg-12-normal;

  @include lg {
    margin-top: 2.4rem;
    justify-content: center;
  }

  a {
    @include tg-12-bold;
    display: inline-flex;
    align-items: center;
    justify-content: center;

    svg {
      margin-left: 0.8rem;
    }
  }
}