@import '../../mixins.scss';

.calendar {
  &__block {
    box-shadow: 2px 2px 16px rgba(51, 74, 86, 0.05);
    border-radius: 24px;
    background-color: $white;
  }

  &__grid {
    display: grid;
    grid-template-columns: repeat(7, 1fr);
    gap: 0.8rem;
  }

  &__title {
    margin-top: 2rem;
    @include tg-14-semibold;

    @include lg {
      display: none;
    }
  }

  &__desc {
    margin-top: 0.4rem;
    @include tg-12-medium;
    opacity: 0.4;

    @include lg {
      display: none;
    }
  }

  &__month {
    transition: $t opacity, $t max-width, $t font-size;
    @include tg-16-medium;
    color: $primary;
    opacity: 0.6;
    max-width: 140px;
    width: 100%;
    text-align: center;
    cursor: pointer;
    white-space: nowrap;
    user-select: none;

    &-selected {
      @include tg-20-semi;
      opacity: 1;
    }
  }

  &__content {
    margin-top: 2.4rem;

    &-inline {
      display: flex;
      flex-direction: column;
      gap: 1.6rem 0;
    }
  }

  &__header {
    padding: 1.8rem 0;
  }

  &__weeks {
    padding: 1.6rem 1.6rem 0 1.6rem;
  }

  &__week {
    @include tg-16-medium;
    color: $primary;
    text-transform: uppercase;
    text-align: center;
  }

  &__inline {
    padding: 24px;
    min-height: 160px;
    cursor: pointer;
    position: relative;
  }

  &__cells {
    padding: 1.6rem;
  }

  &__day {
    position: absolute;
    left: 16px;
    top: 16px;

    @include tg-14-semibold;

    &-inline {
      @include tg-20-semi;
    }

    @include lg {
      position: relative;
      top: unset;
      left: unset;
    }
  }

  &__empty {
    @include tg-14-semibold;
    text-transform: uppercase;
    color: $primary;
    opacity: 0.4;
    text-align: center;
    margin-top: 2rem;
  }

  &__cell {
    position: relative;
    $cell-transition: 0.2s ease;
    width: 16rem;
    height: 14rem;
    transition: $cell-transition box-shadow, $cell-transition transform;
    box-shadow: 2px 2px 16px rgba(51, 74, 86, 0.05);
    border-radius: 2.4rem;
    background-color: $white;
    border: 1px solid rgba(51, 74, 86, 0.08);
    padding: 1.6rem;
    user-select: none;
    cursor: pointer;
    display: flex;
    flex-direction: column;
    align-items: flex-end;

    &-disabled {
      background-color: rgba(51, 74, 86, 0.08);

      .calendar__day {
        opacity: 0.2;
      }
    }

    @include lg {
      width: calc(100%);
      height: 4rem;
      border-radius: 0.8rem;
      padding: 0.4rem;
      align-items: flex-start;
      justify-content: center;
    }

    &:hover {
      box-shadow: 2px 2px 16px rgba(51, 74, 86, 0.07);
      transform: scale(1.03);
    }
  }

  &__nft {
    border-radius: 12px;

    @include lg {
      display: none;
    }
  }
}