@import '../../mixins.scss';

.segments {
  border-radius: 48px;
  border: 2px solid $primary;
  display: flex;
  padding: 4px 6px;
  gap: 0 10px;
  position: relative;
  overflow: hidden;

  &__item {
    transition: $t color;
    color: rgba($primary, 0.4);
    @include tg-14-semibold;
    padding: 8px 16px;
    z-index: 2;
    user-select: none;

    &-selected {
      color: $white;
    }
  }

  &__backdrop {
    transition: $t width, $t transform, $t top;
    width: unset;
    background-color: $primary;
    border-radius: 48px;
    position: absolute;
    left: 0;
    top: 3px;
    height: calc(100% - 6px);
    z-index: 1;
    top: -40px;

    &-inited {
      top: 3px;
    }
  }
}