.title {
  @include tg-40-medium;
  color: $primary;
  @include lg {
    @include tg-24-medium;
  }
  &__main {
    @include create-font(6.8rem, 400, 8rem);
    margin-bottom: 2rem;
    @include lg {
      @include create-font(3.2rem, 400, 4rem);
      margin-bottom: 1.2rem;
    }
  }
  &__pre {
    margin-bottom: 0.4rem;
  }
}

.desc {
  @include lg {
    @include tg-12-unormal;
    color: rgba($primary, 0.6);
  }
}

// base links for all pages
.common-link {
  @include tg-14-semibold;
  color: $primary;
  transition: $t color;
  &:hover {
    color: #24343c;
  }
  &:active {
    color: #546771;
  }
  &:disabled,
  .disabled,
  [disabled] {
    color: #c2c9cc;
    cursor: default;
  }
}

// for swiper
.swiper {
  &-pagination-bullets {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 0 1.2rem;
  }
  &-custom {
    &-bullet {
      background: rgba(51, 74, 86, 0.2);
      width: 0.8rem;
      height: 0.8rem;
      border-radius: 50%;
      &-active {
        width: 1.6rem;
        height: 1.6rem;
        background: $primary;
      }
    }
  }
  &-prev,
  &-next {
    border: 1px solid #334a56;
    border-radius: 100px;
    width: 3.2rem;
    height: 3.2rem;
    color: $primary;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
  }
  &-next {
    color: $primary;
    &__icon {
      transform: rotate(-90deg);
    }
  }
  &-prev {
    &__icon {
      transform: rotate(90deg);
    }
  }
}
