@import '../../../mixins.scss';

.nft {
  display: flex;
  align-items: center;
  white-space: nowrap;

  &__img {
    width: 100px;
    height: 100px;
    border-radius: 16px;
  }

  &__content {
    padding: 1rem 2.4rem;
  }

  &__collection {
    display: flex;
    @include tg-14-semibold;
    color: rgba(51, 74, 86, 0.4);
  }

  &__name {
    @include tg-20-bold;
    margin: 0.8rem 0;
  }

  &__fee {
    display: flex;
    @include tg-14-semibold;
    opacity: 0.6;
  }

  &__alert,
  &__check {
    margin-left: 8px;
  }

  &__check {
    color: #0eaa1e;
  }
}