@import '../../mixins.scss';

.document {
  padding: 2.4rem;
  display: flex;
  flex-direction: column;
  align-items: flex-start;

  background-color: $white;
  box-shadow: 2px 2px 16px rgba(51, 74, 86, 0.05);
  border-radius: 2.4rem;
  margin-bottom: 2.4rem;

  @include lg {
    padding: 1.6rem;
  }

  @include md {
    margin-bottom: 0.8rem;
  }

  &__badge {
    margin-top: 2rem;
  }

  &__title {
    @include tg-20-semi;
  }

  &__check {
    color: #0eaa1e;
  }

  &__date {
    @include tg-14-medium;
    margin-top: 2rem;
    display: flex;

    @include lg {
      @include tg-12-medium;
    }

    @include md {
      width: 100%;
      justify-content: space-between;
    }

    &-title {
      width: 12rem;
      color: rgba(51, 74, 86, 0.6);

      @include md {
        width: auto;
      }
    }
  }
}